import clsx from 'clsx';
import { createContext, useContext } from 'react';
import { ListItemProps, ListProps } from './list.interface';

const ListContext = createContext({ type: 'bullet' });

const List = ({ type = 'bullet', children, className }: ListProps) => {
  const Comp = type === 'bullet' ? 'ul' : 'ol';

  return (
    <ListContext.Provider value={{ type }}>
      <Comp
        className={clsx(
          'zep-typography-bodyText',
          'zep-list-none',
          '[&_ul]:zep-pl-2',
          '[&_ol]:zep-pl-2',
          '[counter-reset:section]',
          className,
        )}
      >
        {children}
      </Comp>
    </ListContext.Provider>
  );
};

const Item = ({ children }: ListItemProps) => {
  const { type } = useContext(ListContext);

  return (
    <li
      className={clsx(
        'zep-relative',
        'zep-flex',
        'zep-items-baseline',
        'zep-py-0.5',
        '[counter-increment:section]',
        'before:zep-w-1.5',
        'before:zep-h-1.5',
        'before:zep-mr-0.75',
        'before:zep-text-center',
        'before:zep-shrink-0',
        { 'before:[content:"â€¢"]': type === 'bullet' },
        { 'before:[content:counter(section,lower-alpha)]': type === 'alphabetical' },
        { 'before:[content:counter(section)]': type === 'numeric' },
      )}
    >
      {children}
    </li>
  );
};

List.Item = Item;

export { List };
